      }
    },
    showSubTitle(e) {
      let id = e.currentTarget.dataset.subindex;
      this.showSubIndex = id;
    },
    mouseOnItem(){
      clearTimeout(timer);
    },
    closeSubTitle() {
      timer = setTimeout(() => {
        console.log('debounce exe');
        this.showSubIndex = 0;
      }, 200);
    },
    debounce(fn, delay) {
      console.log('debounce');
      var delay = delay || 200;
      var timer;
      return function () {
          var th = this;
          var args = arguments;
          if (timer) {
              clearTimeout(timer);
          }
          timer = setTimeout(function () {
              timer = null;
              fn.apply(th, args);
          }, delay);
      };
    }
  },
